@font-face {
  font-family: Inter;
  src: url("Inter-Regular.74535f8a.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Medium.de3d51e6.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: Inter;
  src: url("Inter-Bold.2a398d70.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}

body {
  color: #1d1d1f;
  background-color: #f5f5f7;
  font-family: Inter, sans-serif;
}

.container {
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 375px) and (max-width: 1575px) {
  .container {
    width: 375px;
    padding-left: 4px;
    padding-right: 4px;
  }
}

@media screen and (min-width: 1576px) {
  .container {
    width: 1576px;
  }
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

img {
  width: 100%;
  display: block;
}

.header-container {
  position: relative;
}

.header-wrapper {
  background-color: #fff;
  border-radius: 12px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

@media screen and (min-width: 320px) {
  .header-wrapper {
    padding: 8px 12px;
  }
}

@media screen and (min-width: 1576px) {
  .header-wrapper {
    padding: 12px 15px;
  }
}

.location {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 1576px) {
  .location {
    width: 150px;
  }
}

.header-list {
  list-style: none;
  display: flex;
}

@media screen and (min-width: 320px) {
  .header-list {
    gap: 4px;
  }
}

@media screen and (min-width: 1576px) {
  .header-list {
    gap: 12px;
  }
}

.header-list__item {
  align-items: center;
  display: flex;
}

.header-list__link {
  border: 1px solid #d2d2d7;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 320px) {
  .header-list__link {
    width: 38px;
    height: 38px;
  }
}

@media screen and (min-width: 1576px) {
  .header-list__link {
    width: 54px;
    height: 54px;
  }
}

.nav-list {
  list-style: none;
}

@media screen and (min-width: 320px) {
  .nav-list {
    display: none;
  }
}

@media screen and (min-width: 1576px) {
  .nav-list {
    align-items: center;
    gap: 48px;
    display: flex;
  }
}

.nav-list__link {
  color: inherit;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  text-decoration: none;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.nav-list__link:hover, .nav-list__link:focus {
  color: #f63;
}

.phone-correction {
  align-items: center;
  display: flex;
}

.cart-btn {
  transition-property: background-color, color;
  transition-duration: .25s, .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1), cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 1576px) {
  .cart-btn {
    color: #686870;
    cursor: pointer;
    width: 147px;
    height: 56px;
    background-color: #fff;
    border: 1px solid #d2d2d7;
    border-radius: 12px;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 16px 24px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5;
    display: flex;
  }
}

.cart-btn:hover, .cart-btn:focus {
  color: #fff;
  background-color: #f63;
}

@media screen and (min-width: 320px) {
  .hide-cart-btn {
    display: none;
  }
}

@media screen and (min-width: 1576px) {
  .hide-cart-btn {
    cursor: pointer;
    display: block;
  }
}

@media screen and (min-width: 320px) {
  .hide-menu-btn {
    display: block;
  }
}

@media screen and (min-width: 1576px) {
  .hide-menu-btn {
    display: none;
  }
}

.menu-btn {
  width: 40px;
  height: 40px;
  background-color: #f5f5f7;
  border: 1px solid #d2d2d7;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu-btn:hover, .menu-btn:focus {
  fill: #fff;
  background-color: #f63;
}

.location-btn {
  background-color: inherit;
  border: 0;
  align-items: flex-end;
  font-weight: 500;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (min-width: 320px) {
  .location-btn {
    font-size: 12px;
    line-height: 1.33;
  }
}

@media screen and (min-width: 1576px) {
  .location-btn {
    cursor: pointer;
    font-size: 16px;
    line-height: 1.5;
  }
}

.location-btn:hover, .location-btn:focus {
